import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

// import { AuthService } from '../../../services/auth/auth.service';

import { BlankLayoutCardComponent } from 'app/components/blank-layout-card';
import { ApiService } from 'app/services/api.service';

@Component({
  selector: 'app-sign-up',
  styleUrls: ['../../../components/blank-layout-card/blank-layout-card.component.scss'],
  templateUrl: './sign-up.component.html',
})
export class SignUpComponent extends BlankLayoutCardComponent implements OnInit {

  public signupForm: FormGroup;
  public emails;
  public passwords;
  private usernames;
  public email;
  public password;
  public password2;
  public username;
  public emailPattern = '^([a-zA-Z0-9_\\-\\.]+)@([a-zA-Z0-9_\\-\\.]+)\\.([a-zA-Z]{2,5})$';
  public error: string;

  constructor(private apiService: ApiService,
              private fb: FormBuilder,
              private router: Router) {
    super();

    this.signupForm = this.fb.group({
      password: new FormControl('', Validators.required),
      password2: new FormControl('', Validators.required),
      email: new FormControl('', [
        Validators.required,
        Validators.pattern(this.emailPattern),
        Validators.maxLength(50),
      ]),
      username: new FormControl('', [Validators.required, Validators.maxLength(50)]),
    });
    this.email = this.signupForm.get('email');
    this.password = this.signupForm.get('password');
    this.username = this.signupForm.get('username');
  }


  public ngOnInit() {
    this.signupForm.valueChanges.subscribe(() => {
      this.error = null;
    });
  }


  public onInputChange(event: any) {
    event.target.required = true;
    const username = event.target.value;
    this.usernames = username;
  }

  public onInputChange2(event: any) {
    event.target.required = true;
    const email = event.target.value;
    this.emails = email;
  }

  public onInputChange3(event: any) {
    event.target.required = true;
    const password = event.target.value;
    this.passwords = password;
  }

  public onInputChange4(event: any) {
    event.target.required = true;
  }

  login(){
    this.apiService.register(this.usernames, this.emails, this.passwords).subscribe();

  }

}
