import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

// import { AuthService } from '../../../services/auth/auth.service';
import { BlankLayoutCardComponent } from 'app/components/blank-layout-card';
import { Teacher } from 'app/models/teacher.model';
import { ApiService } from 'app/services/api.service';
import { HttpResponse } from '@angular/common/http';
import { IApiResponse } from 'app/services/api';
import swal from 'sweetalert2';
import { Student } from 'app/models/student.model';

@Component({
  selector: 'app-login',
  styleUrls: ['../../../components/blank-layout-card/blank-layout-card.component.scss'],
  templateUrl: './login.component.html',
})
export class LoginComponent extends BlankLayoutCardComponent implements OnInit {
  public loginForm: FormGroup;
  public username;
  public password;
  public error;
  keepLoggedIn: boolean;
  responseBody: Array<Teacher>;
  responseBody2: Array<Student>;


  constructor(private fb: FormBuilder,
              private router: Router,
              @Inject(ApiService) private apiService: ApiService,) {
    super();

    this.loginForm = this.fb.group({
      password: new FormControl('', Validators.required),
      username: new FormControl('', [Validators.required, Validators.maxLength(50),]),
    });
    this.password = this.loginForm.get('password');
    this.username = this.loginForm.get('username');
    // this.keepLoggedIn = false;
    
  }

  public ngOnInit() {
    localStorage.setItem('userName', '');
    localStorage.setItem('token', '');
    localStorage.setItem('courseId', '');
    localStorage.setItem('questionId', '');
  }

  onChangeKeepLoggedIn(e): void {
		this.keepLoggedIn = e.target.checked;
  }
  

  public onInputChange(event: any) {
    event.target.required = true;
    const username = event.target.value;
    this.username = username;
  }

  public onInputChange2(event: any) {
    event.target.required = true;
    const password = event.target.value;
    this.password = password;
  }

  onLogin() {
		this.apiService.login(this.username, this.password).subscribe((response: any) => {
			if (response) {
        if(!this.apiService.studentStatus){
          const loginSuccess: boolean = response === true;
          const responseBody = this.apiService.teachers;
          const userName = this.apiService.teachers.teacher_user;
          const token = 'teacher';
          if (loginSuccess) {
            if (responseBody) {
              localStorage.setItem('isLoggedIn', 'true');
              localStorage.setItem('userName', userName);
              localStorage.setItem('token', token);
              swal.fire(
                'Login Success!',
                'Welcome to Java Codewars!',
                'success'
              ).then(() => {
                this.router.navigate(['/teacher/teacher-home']);
              });
            } else {
              swal.fire(
                'Login Error!',
                'Something went wrong, please contact administrator!',
                'error'
              ).then(() => {
                // this.refresh();
              });
            }
          } else {
            swal.fire(
              'Login Error!',
              'E-mail or password is incorrect.',
              'error'
            ).then(() => {
              // this.refresh();
            });
          }
        } else {
          
          const loginSuccess: boolean = response === true;
          const responseBody2 = this.apiService.students;
          const userName = this.apiService.students.student_id;
          const token = 'student';
          if (loginSuccess) {
            if (responseBody2) {
              localStorage.setItem('isLoggedIn', 'true');
              localStorage.setItem('userName', userName);
              localStorage.setItem('token', token);
              swal.fire(
                'Login Success!',
                'Welcome to Java Codewars!',
                'success'
              ).then(() => {
                this.apiService.studentCourseAll().subscribe( () => {});
                this.router.navigate(['/student/student-home']);
              });
            } else {
              swal.fire(
                'Login Error!',
                'Something went wrong, please contact administrator!',
                'error'
              ).then(() => {
                // this.refresh();
              });
            }
          } else {
            swal.fire(
              'Login Error!',
              'E-mail or password is incorrect.',
              'error'
            )
          }
        }
			} else {
				swal.fire(
					'Login Error!',
					'No Response',
					'error'
				).then(() => {
          this.refresh();
        });
			}
		});
  }
  
  refresh(): void {
    window.location.reload();
  }

}
